.navbar-brand:hover .pulse-in {
	animation: pulseIn ease 1s;
	-webkit-animation: pulseIn ease 1s;
	-moz-animation: pulseIn ease 1s;
	-o-animation: pulseIn ease 1s;
	-ms-animation: pulseIn ease 1s;
}
@keyframes pulseIn {
	0% {
		transform: scale(1, 1);
	}
	20% {
		transform: scale(0.5, 0.5);
	}
	100% {
		transform: scale(1, 1);
	}
}

@-webkit-keyframes pulseIn {
	0% {
		transform: scale(1, 1);
	}
	20% {
		transform: scale(0.5, 0.5);
	}
	100% {
		transform: scale(1, 1);
	}
}

