.card {
	z-index: 0;
	overflow: hidden;
}
.card:before {
	content: '';
	position: absolute;
	z-index: -1;
	bottom: -128px;
	right: -128px;
	height: 128px;
	width: 128px;
	border-radius: 5px;
	transform: scale(1);
	transform: rotate(90deg);
	transform-origin: 50% 50%;
	transition: transform 0.75s ease-out;
	transition: transform 1s ease-out;
}

.card:hover:before {
	transform: scale(21);
}

.card-corner-color-primary:before {
	background: #f27127;
}

.card-corner-color-secondary:before {
	background: #3f84e5;
}

/*~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ 
//~~~ ON-HOVER FUNCTIONALITY ~~~
//~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~*/
.hover-parent {
}
  
.hover-parent .hover-child{
    visibility: hidden;
}

.hover-parent:hover .hover-child {
    visibility: visible;
}

/*~~~~~~~~~~~~~~~~~~~~~~~~~~
//~~~ ON-HOVER ANIMATIONS~~~
//~~~~~~~~~~~~~~~~~~~~~~~~~~*/
/* --- Fade-In Full --- */
.hover-parent:hover .fade-in-full {
    animation: fadeInFull ease 1.5s;
    -webkit-animation: fadeInFull ease 1.5s;
    -moz-animation: fadeInFull ease 1.5s;
    -o-animation: fadeInFull ease 1.5s;
    -ms-animation: fadeInFull ease 1.5s;
    }
    @keyframes fadeInFull {
    0% {opacity:0;}
    100% {opacity:1;}
    }
    
    @-webkit-keyframes fadeInFull {
    0% {opacity:0;}
    100% {opacity:1;}
    }

/* --- Fade-In Half --- */
.hover-parent:hover .fade-in-half {
    animation: fadeInHalf ease 1.5s;
    -webkit-animation: fadeInHalf ease 1.5s;
    -moz-animation: fadeInHalf ease 1.5s;
    -o-animation: fadeInHalf ease 1.5s;
    -ms-animation: fadeInHalf ease 1.5s;
    }
    @keyframes fadeInHalf {
    0% {opacity:0;}
    100% {opacity:0.5;}
    }
    
    @-webkit-keyframes fadeInHalf {
    0% {opacity:0;}
    100% {opacity:0.5;}
    }


